.search-room {
  &__main {
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: 600px) {
      display: block;
    }
  }
  &__sidebar {
    flex-shrink: 0;
    flex-basis: 266px;
    margin-right: 5.1%;
  }
  &__filterDateDropdown {
    width: 100%;
    margin-top: 50px;
  }
  &__dropdown-guests {
    margin-top: 41px;
    margin-bottom: 29px;
  }
  &__checkbox-btn {
    margin-top: 38px;
    margin-bottom: 31px;
  }
  &__dropdown-rooms {
    margin-top: 47px;
    margin-bottom: 30px;
  }
  &__rooms {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -6px;
  }
  &__title {
    @extend %h1;
    margin-top: 28px;
    margin-bottom: 19px;
  }
  &__pagination {
    margin-left: auto;
    margin-right: auto;
    width: 16em;
    margin-top: 19px;
  }
  &__card {
    margin-bottom: 21px;
    margin-left: 6px;
    margin-right: 6px;
  }
  &__footer {
    &_wide {
      border-top: 1px solid $dark25;
      margin-top: 75px;
    }
  }
  &__range-slider {
    padding-top: 9px;
  }
}
